import React, { Component } from 'react';
import { Card } from 'antd';
import styles from './index.module.less';
import LeftTree from './leftTree';
import RightTable from './rightTable';
import DataTabs from '../common/dataTab';
//
export default class dataSetManagement extends Component {
    constructor(props){
        super(props);
        this.state = {
            searchVal:"",
            selectedFolder:{},//
        }
    }
    changeSelectedFolder = (selectedFolder) => {
        this.setState({selectedFolder})
    }
    render() {
        return (
            <React.Fragment>
                <DataTabs activeTab="dataSetManagement"/>
                <div style={{padding:"20px"}}>
                    <Card
                        title={false}
                        bodyStyle={{padding:0}}
                    >
                        <div className={styles.bodyContainer}>
                            <LeftTree OutSelectedFolder={this.state.selectedFolder} changeSelectedFolder={(selectedFolder)=>{this.changeSelectedFolder(selectedFolder)}}/>
                            <div className={styles.line}></div>
                            <RightTable selectedFolder={this.state.selectedFolder} changeSelectedFolder={this.changeSelectedFolder}/>
                        </div>
                    </Card>
                </div>
            </React.Fragment>
            
        )
    }
}
