import React, { Component } from 'react';
import { Table, Divider, Button, Modal, Popconfirm, message, Input} from 'antd';
import moment from 'moment';
import DataSetModal from './dataSetModal';
import styles from './index.module.less';
import { getAction, postAction } from '@/axios';
import {hasButtonPermission} from '@/util'
export default class RightTable extends Component {
    constructor(props){
        super(props);
        this.state = {
            columns:[
                {
                    title:"",
                    dataIndex:"sortIndex",
                    width:60,
                    fixed:"left"
                },{
                    title:"",
                    dataIndex:"dataSetName",
                    render:text => (<span title={text}>{text}</span>),
                    // width:200
                }
                ,{
                    title:"",
                    dataIndex:"dataSetType",
                    render:text => {
                        if(text === 1){
                            return <span title=''></span>
                        }else if(text === 2){
                            return <span title=''></span>
                        }
                    },
                    // width:200
                }
                ,{
                    title:"",
                    dataIndex:"createByName",
                    render:text => (<span title={text}>{text}</span>),
                    // width:120
                },{
                    title:"",
                    dataIndex:"createTime",
                    // width:120,
                    render:(text) => moment.unix(text/1000).format("YYYY/MM/DD HH:mm:ss")
                },{
                    title:"",
                    dataIndex:"updateByName",
                    render:text => (<span title={text}>{text}</span>),
                    // width:120
                },{
                    title:"",
                    dataIndex:"updateTime",
                    // width:200,
                    render:(text) => moment.unix(text/1000).format("YYYY/MM/DD HH:mm:ss")
                },{
                    title:"",
                    dataIndex:"folderName",
                    render:text => (<span title={text}>{text}</span>),
                    // width:120
                },{
                    title:"",
                    width:120,
                    fixed:"right",
                    render:(text,record) => (
                        <React.Fragment>
                            {
                                hasButtonPermission('dataSet005') &&
                                <span className="operation-link" onClick={()=>{this.edit(record)}}></span>

                            }
                            {
                                hasButtonPermission('dataSet006') &&
                                <React.Fragment>
                                    <Divider type="vertical"/>
                                    <Popconfirm
                                        title=""
                                        okText=""
                                        cancelText=""
                                        onConfirm={()=>{this.delete(record)}}
                                    >
                                        <span className="operation-link"></span>
                                    </Popconfirm>
                                </React.Fragment>
                            }
                            
                        </React.Fragment>
                    )
                }
            ],
            dataSource:[
                // {sortIndex:"1",dataSetName:"",createByName:"",createTime:12345678,updateByName:"",updateTime:1565337495000,folderName:"1",dataSetId:"99"},
            ],
            modalType:"add",
            showModal:false,
            record:{},//
            searchVal:"",
            pageInfo:{
                current:1,
                pageSize:10,
                total:0,
                showTotal:(total) => `${total}`
            }
        }
    }
    componentDidMount(){
        this.getTableList();
    }
    componentDidUpdate(prevProps){
        if(prevProps.selectedFolder.key !== this.props.selectedFolder.key && prevProps.selectedFolder.key!=="000"){
            this.setState({
                searchVal:""
            },()=>{this.getTableList()})
        }
        
    }
    //---
    getTableList = (type) => {
        const dataSetFolderId = this.props.selectedFolder.key || "";
        const dataSetName = this.state.searchVal;
        let pageNum = this.state.pageInfo.current;
        const pageSize = this.state.pageInfo.pageSize;
        if(type==="reset"){
            pageNum=1;
        }
        getAction(`/ybt/data/dataset/folder/list?dataSetFolderId=${dataSetFolderId}&dataSetName=${dataSetName}&pageNum=${pageNum}&pageSize=${pageSize}`).then(res => {
            if(res.success){
                this.setState({
                    dataSource:res.obj.rows.map((item,index)=>({...item,sortIndex:index+1})),
                    pageInfo:{
                        current:res.obj.page,
                        pageSize:res.obj.pageSize,
                        total:res.obj.total,
                        showTotal:(total) => `${total}`
                    },
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    edit = (record) => {
        this.setState({showModal:true,modalType:"edit",record})
    }
    //---
    delete = (record) => {
        getAction(`/ybt/data/dataset/delete?dataSetId=${record.dataSetId}`).then(res => {
            if(res.success){
                message.success("!")
                this.getTableList("reset")
            }else{
                message.error(res.obj)
            }
        })
    }
    add = () => {
        this.setState({showModal:true,modalType:"add",record:{}})
    }
    //
    handleCancle = () => {
        this.setState({showModal:false})
    }
    /// ---
    handleOK = (params) => {
        if(this.state.modalType!=="add"){
            params = {...params,dataSetId:this.state.record.dataSetId}
        }
        postAction("/ybt/data/dataset/save",params).then(res => {
            if(res.success){
                this.setState({showModal:false},()=>{this.getTableList("reset")});
            }else{
                message.error(res.obj);
            }
        })
        
    }
    handleSearch = (val) => {
        this.setState({searchVal:val},() => {this.getTableList()});
    }
    //
    handleReset=()=>{
        //folderId
        this.props.changeSelectedFolder({key:''});
        // //
        let pageInfo = this.state.pageInfo;
        pageInfo.current = 1;
        pageInfo.pageSize = 10;

        this.setState({ 
            searchVal:'',
            pageInfo
        });
    }
    // --
    dataSetNameChange = (e) => {
        this.setState({ searchVal:e.target.value })
    }
    changePagination = (pagination) => {
        this.setState({
            pageInfo:{
                ...this.state.pageInfo,
                current:pagination.current
            }
        },this.getTableList)
    }
    render() {
        return (
            <div className={styles.rightTable}>
                <div style={{display:"flex",marginBottom:"10px"}} className={styles.searchBox}>
                    <div style={{flex:"0 0 100px",lineHeight:"32px"}}></div>
                    <div style={{flex:"1"}}><Input.Search placeholder="" allowClear value={this.state.searchVal}  onChange={this.dataSetNameChange} onSearch={this.handleSearch}/></div>
                    <Button icon="sync" style={{marginLeft:12}} onClick={this.handleReset}></Button>
                </div>
                <div style={{marginBottom:"10px"}} className={styles.btn}>
                    {
                        hasButtonPermission('dataSet004') &&
                        <Button icon="plus" type="primary" onClick={this.add}></Button>

                    }
                </div>
                <Table
                    className={styles.table}
                    rowKey="sortIndex"
                    dataSource={this.state.dataSource}
                    columns={this.state.columns}
                    pagination={this.state.pageInfo}
                    onChange={this.changePagination}
                    scroll={{x:"100%"}}
                />
                <Modal
                    visible={this.state.showModal}
                    title={this.state.modalType==="add"?"":""}
                    onCancel={this.handleCancle}
                    footer={false}
                    destroyOnClose={true}
                    width={900}
                >
                    <DataSetModal 
                        modalType={this.state.modalType} 
                        recordInfo={this.state.record}
                        handleCancle={this.handleCancle}
                        handleOK={(params)=>{this.handleOK(params)}}
                    />
                </Modal>
            </div>
        )
    }
}