import React, { Component } from 'react';
import { Input, Select, TreeSelect, Button, Tag, Table, message } from 'antd';
import styles from './index.module.less';
import { postAction, getAction } from '@/axios';

const { TreeNode } = TreeSelect;

let count = 0;

export default class DataSetModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            options:[
                // {dataSourceId:"",dataSourceName:""}
            ],
            treeData:[
                // {
                //     folderName:"",
                //     dataSetFolderId:"000",
                //     subDataSetFolders:[
                //         {
                //             folderName:"1",
                //             dataSetFolderId:"001"
                //         }
                //     ]
                // }
            ],
            tableSourceFilter:"",//
            tableSource:[
                // {name:"",sortIndex:"1"}
            ],
            fieldTableColumns:[
                {
                    title:"",
                    dataIndex:"name",
                    width:230
                },{
                    title:"",
                    dataIndex:"type",
                    width:230
                },{}
            ],
            fieldTableData:[
                // {name:"",type:"number",index:"0"},
            ],//table
            curTableRecord:{},//
            textAreaValue:"",
            tagsList:[//
                // {fieldType:"99",fieldName:"city"},
            ],
            dataSetName:"",//
            dataSource:"",//
            folder:"",//
            dataSetType:'',
            dataSetTypeOption:[
                {
                    name:'',
                    value:1
                },
                {
                    name:'',
                    value:2
                },
            ]
        }
    }
    componentDidMount(){
        this.getFolderTree();
        this.getDataSourceList();
        this.setState({
            dataSetName:this.props.recordInfo.dataSetName,
            dataSource:this.props.recordInfo.dataSourceId,
            folder:this.props.recordInfo.dataSetFolderId,  
            dataSetType:this.props.recordInfo.dataSetType
        })
        if(this.props.recordInfo.dataSetId){
            this.getTagsByDataSet(this.props.recordInfo.dataSetId);
            this.getInitInfo(this.props.recordInfo.dataSetId)
        }
    }
    //
    getInitInfo = (dataSetId) => {
        getAction(`/ybt/data/dataset/info?dataSetId=${dataSetId}`).then(res => {
            if(res.success){
                this.setState({
                    dataSetName:res.obj.dataSetName,
                    dataSource:res.obj.dataSourceId,
                    folder:res.obj.dataSetFolderId,
                    textAreaValue:res.obj.querySql
                },this.getdataSourceData)
            }
        })
    }
    //id
    getTagsByDataSet = (dataSetId) => {
        getAction(`/ybt/data/dataset/field/list?dataSetId=${dataSetId}`).then(res => {
            if(res.success){
                this.setState({
                    tagsList:res.obj.map(item => ({fieldType:item.fieldType,fieldName:item.fieldName}))
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //
     inputChange = (e) => {
        this.setState({dataSetName:e.target.value})
    }
    //
    getDataSourceList = () => {
        getAction("/ybt/data/datasource/list?dataSourceName=").then(res => {
            if(res.success){
                this.setState({options:res.obj})
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    getFolderTree = () => {
        getAction("/ybt/data/dataset/folder/trees").then(res => {
            if(res.success){
                this.setState({
                    treeData:res.obj || []
                })
            }else{
                message.error(res.msg)
            }
        })
    }
    //
    renderTree = (data=[]) => data.map(item => {
        if(item.subDataSetFolders && item.subDataSetFolders.length>0){
            return <TreeNode title={item.folderName} key={item.dataSetFolderId} value={item.dataSetFolderId}>
                {this.renderTree(item.subDataSetFolders)}
            </TreeNode>
        }else{
            return <TreeNode title={item.folderName} key={item.dataSetFolderId} value={item.dataSetFolderId}></TreeNode>
        }
    })
     //
    changeFolder = (val) => {
        this.setState({folder:val})
    }
     //
     changeDataSource = (val) => {
        this.setState({dataSource:val},()=>{this.getdataSourceData()})
    }
    changedataSetType=(val)=>{
        this.setState({dataSetType:val})
    }
    //----
    getdataSourceData = () => {
        getAction(`/ybt/data/dataset/datasource/table/list?dataSourceId=${this.state.dataSource}`).then(res => {
            if(res.success){
                this.setState({
                    tableSource:res.obj.map((item,index) => ({name:item.name,sortIndex:index+1})),
                    tableSourceFilter:"",
                    curTableRecord:{}
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    filterTableList = (val) => {
        this.setState({
            tableSourceFilter:val,
            curTableRecord:{}
        })
    }
    //table
    handleClick = (item) => {
        count += 1;
        setTimeout(() => {
            if (count === 1) {//
                this.setState({
                    curTableRecord:item
                },this.getTableList)
            } else if (count >= 2) {//
                this.setState({
                    curTableRecord:item
                },this.getTableSQL)
            }
            count = 0;
        }, 300);
    }
    //table
    getTableList = () => {
        // message.info("table...");
        const dataSourceId = this.state.dataSource;
        const table = this.state.curTableRecord.name;
        getAction(`/ybt/data/dataset/datasource/table/column/list?dataSourceId=${dataSourceId}&table=${table}`).then(res => {
            if(res.success){
                this.setState({
                    fieldTableData:res.obj.map((item,index) => ({...item,sortIndex:index}))
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //SQL
    getTableSQL = () => {
        // message.info("SQL...");
        const dataSourceId = this.state.dataSource;
        const table = this.state.curTableRecord.name;
        getAction(`/ybt/data/dataset/datasource/table/sql?dataSourceId=${dataSourceId}&table=${table}`).then(res => {
            if(res.success){
                this.setState({
                    textAreaValue:`${this.state.textAreaValue}\n${res.obj}`
                })
            }else{
                message.error(res.obj);
            }
        })
    }
    //SQL
    changeSQL = (e) => {
        this.setState({textAreaValue:e.target.value})
    }
    //
    handleRun = () => {
        const params = {
            dataSourceId:this.state.dataSource,
            sql:this.state.textAreaValue
        }
        postAction("/ybt/data/dataset/sql/fieldList",params,0,1).then(res => {
            if(res.success){
                message.success("");
                this.setState({
                    tagsList:res.obj.map(item => ({
                            fieldName:item.name,
                            fieldType:item.type
                        })
                    )
                })
            }else{
                this.setState({
                    tagsList:[]
                })
                message.error(res.obj)
            }
        })
    }
    //
    handleOK = () => {
        if(this.state.dataSetName===""){
            message.warn("");
            return false
        }
        if(this.state.dataSource===""){
            message.warn("");
            return false
        }
        if(this.state.folder===""){
            message.warn("");
            return false
        }
        if(this.state.tagsList.length===0){
            message.warn("");
            return false
        }
        const params = {
            dataSetName:this.state.dataSetName,
            dataSourceId:this.state.dataSource,
            dataSetFolderId:this.state.folder,
            querySql:this.state.textAreaValue,
            dataSetFields:this.state.tagsList,
            dataSetType:this.state.dataSetType
        }
        this.props.handleOK(params);
    }
    //
    handleCancle = () => {
        this.props.handleCancle()
    }
    render() {
        const tableSource = this.state.tableSource.filter(item => item.name.indexOf(this.state.tableSourceFilter) !== -1).map((item,index) => ({...item,sortIndex:index+1}));
        return (
            <React.Fragment>
                <div className={styles.row}>
                    <span className={styles.label}><i style={{color:"red"}}>*</i> </span>
                    <Input placeholder="" style={{width:710}} onChange={this.inputChange} value={this.state.dataSetName}/>
                </div>
                <div className={styles.row}>
                    <span className={styles.label}> </span>
                    <Select placeholder="" style={{width:300}}  onChange={this.changeDataSource} value={this.state.dataSource}>
                        {this.state.options.map(item => (<Select.Option key={item.dataSourceId} value={item.dataSourceId}>{item.dataSourceName}</Select.Option>))}
                    </Select>
                    <span className={styles.label}> </span>
                    <TreeSelect
                        placeholder="" 
                        style={{width:290}}
                        // treeData={this.state.treeData}
                        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                        onChange={this.changeFolder}
                        value={this.state.folder}
                    >
                        {this.renderTree(this.state.treeData)}
                    </TreeSelect>
                </div>
                <div className={styles.row}>
                    <span className={styles.label}> </span>
                    <Select placeholder="" style={{width:300}}  onChange={this.changedataSetType} value={this.state.dataSetType}>
                        {this.state.dataSetTypeOption.map(item => (<Select.Option key={item.value} value={item.value}>{item.name}</Select.Option>))}
                    </Select>
                </div>
                <div className={styles.row}>
                    <span className={styles.label}></span>
                    <div className={styles.tableCenter}>
                        <div className={styles.left}>
                            <Input.Search placeholder="" onSearch={this.filterTableList} style={{width:270,margin:"0 auto",display:"block"}}/>
                            <ul className={styles.tableList}>
                                {tableSource.map((item,index) => (
                                    <li 
                                        key={index} 
                                        onClick={()=>{this.handleClick(item)}}
                                        className={item.name===this.state.curTableRecord.name?styles.active:""}
                                        // onDoubleClick={() => {this.handleGetSQL(item)}}
                                    >
                                        <span>{item.sortIndex}</span>{item.name}
                                    </li>)
                                )}
                            </ul>
                        </div>
                        <div className={styles.right}>
                            <Table
                                rowKey="sortIndex"
                                columns={this.state.fieldTableColumns}
                                dataSource={this.state.fieldTableData}
                                scroll={{y:200}}
                                pagination={false}
                            />
                        </div>
                    </div>
                </div>
                <div className={styles.row}>
                    <div className={styles.leftSQL}>
                        <span onClick={this.handleRun}></span>
                        <div className={styles.border}>
                            <Input.TextArea style={{width:500,height:154,border:"none"}} onChange={this.changeSQL} value={this.state.textAreaValue}/>
                        </div>
                    </div>
                    <div className={styles.rightTags}>
                        
                        <div className={styles.border}>
                            {this.state.tagsList.map((item,index) =><Tag key={item.fieldName+index} color="#3F54A2">{item.fieldName}</Tag>)}
                        </div>
                    </div>
                </div>
                <div className={styles.btns}>
                    <Button type="primary" style={{marginRight:15}} onClick={this.handleOK}></Button>
                    <Button onClick={this.handleCancle}></Button>
                </div>
            </React.Fragment>
        )
    }
}
